import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import LoginByPhone from '@/components/LoginByPhone'
import MyLearn from '@/view/MyLearn'
import TSLearn from '@/view/TSLearn'
import TSClone from '@/view/TSClone'
import AuthInfo from '@/view/AuthInfo'
import AxiosLearn from '@/view/AxiosLearn'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/fill_info',
      name: 'fill_info',
      component: LoginByPhone
    },
    {
      path: '/my_learn',
      name: 'my_learn',
      component: MyLearn
    },
    {
      path: '/ts_learn',
      name: 'ts_learn',
      component: TSLearn
    },
    {
      path: '/ts_clone',
      name: 'ts_clone',
      component: TSClone
    },
    {
      path: '/auth_info',
      name: 'auth_info',
      component: AuthInfo
    },
    {
      path: '/axios_learn',
      name: 'axios_learn',
      component: AxiosLearn
    }

  ]
})



