









import Vue from 'vue'
import Component from 'vue-class-component'

// 默认template中绑定的就是TSLearn VUE
@Component
export default class TSLearn extends Vue {
  // 初始化数据
  private msg: any = 123

  // 声明周期钩子
  mounted() {
    // this.greet()
    this.msg += 123;
  }

  // 计算属性
  get getMessage() {
    return 'computed 1' + this.msg
  }

  // 方法
  greet() {
    alert('greeting: ' + this.msg)
  }
}



