<template>
  <div id="demo">
    <van-cell-group>
      <van-field v-model="name" label="用户名" @input="inputPhone" required clearable
                 placeholder="请输入用户名"/>

      <van-field v-model="phone" label="手机号" @input="inputPhone" required clearable @clear="clearPhone"
                 placeholder="请输入手机号" :error-message="errorMessage"/>

      <van-field v-model="address" label="所在城市" required clearable placeholder="当前您所在城市"
                 :error-message="errorMessage"/>

      <van-field v-model="advantage" label="优势" clearable rows="4"
                 autosize placeholder="您自身的优势"/>

      <van-field v-model="needHelp" rows="4"
                 autosize label="帮助" clearable placeholder="您需要得到那些帮助"/>

      <van-field v-model="teacherMemo" rows="4"
                 autosize label="教练寄语" required clearable placeholder="您对健康管理的心得、体会"/>

    </van-cell-group>

    <van-button size="large" :disabled= "isDisable" @click="handleSubmit" type="danger">确认</van-button>

  </div>
</template>

<script>
  import {
    Loading,
    Button,
    Popup,
    CellGroup,
    Field,
  } from 'vant'

  //demo1.vue
  // export const str = 'hello world'
  //
  // export function f(a){
  //   return a+1
  // }

  // 对应的引入方式：
  // //demo2.js
  // import { str, f } from 'demo1'

  // export default 只能导出一个默认模块，这个模块可以匿名
  //另外 export default和export的区别是 前者只能导出一个 ，后者可以导出若干个
  //vm = new Vue({}) ->创建一个Vue的实例 就是相当于创建一个根组件
  export default {
    name: 'my_name',
    // 引用并使用vant组件
    components: {
      "van-loading": Loading,
      "van-popup": Popup,
      "van-cell-group": CellGroup,
      "van-field": Field,
      "van-button": Button,

    },
    data() {
      // data是要return出去的
      return {
        name: '', // 接收v-model的绑定
        phone: '',
        address: '',
        advantage: '',
        needHelp: '',
        teacherMemo: '',

        isDisable: true
      }
    },
    methods: {
      handleSubmit() {
        console.log(this.name, this.phone, this.address, this.advantage, this.needHelp, this.teacherMemo)
        alert('提交注册的ajax请求')
      },

      // 输入手机号
      inputPhone() {
        this.phone = this.phone.replace(/\D|^0/g, "").slice(0, 11);
        if (this.phone.length === 11 && this.name.length >= 2) {
          this.data.isDisable = false;
        } else {
          this.data.isDisable = true;
        }
      }

    }

  }

</script>

<style>

</style>



