<template>
  <div id="demo">
    <van-field v-model="this.$store.state.user.name" label="所在城市" required clearable @input="inputPhone" placeholder="当前您所在城市"
               :error-message="errorMessage"/>

    <van-button @click="handleSubmit" slot="button" size="small" type="primary">发送验证码</van-button>
  </div>
</template>

<script>
  import {
    Loading,
    Button,
    Popup,
    CellGroup,
    Field,
  } from 'vant'

  // export default 只能导出一个默认模块，这个模块可以匿名
  //另外 export default和export的区别是 前者只能导出一个 ，后者可以导出若干个
  //vm = new Vue({}) ->创建一个Vue的实例 就是相当于创建一个根组件
  export default {
    name: 'my_name',
    // 引用并使用vant组件
    components: {
      "van-loading": Loading,
      "van-popup": Popup,
      "van-cell-group": CellGroup,
      "van-field": Field,
      "van-button": Button,

    },
    data() {
      // data是要return出去的
      return {
        username: 'wwww', // 接收v-model的绑定
        password: '123',
      }
    },
    methods: {
      handleSubmit() {
        console.log(this.username, this.password)
        this.$store.commit('setUser',{name: 'my name is MyLearn'})
        // alert('提交注册的ajax请求')
      }
    }

  }


  // new Vue({
  //   el: '#demo',
  //   data: {
  //     username: 'wwww',
  //     password: '123',
  //     sex: '男',
  //     likes: ['foot'],
  //     allCitys: [{id: 1, name: 'BJ'}, {id: 2, name: 'SS'}, {id: 3, name: 'SZ'}],
  //     cityId: '2',
  //     info: ''
  //   },
  //   methods: {
  //     handleSubmit () {
  //       console.log(this.username, this.pwd, this.sex, this.likes, this.cityId, this.info)
  //       alert('提交注册的ajax请求')
  //     }
  //   }
  // })

</script>

