













import {
  Component,
  Vue
} from "vue-property-decorator";
import {
  Button,
  Lazyload
} from "vant";
import {
  Godownload,
  getParams,
  isWeiXin,
  getCookie,
  addCookie
} from "../utils";
import Axios from "../utils/request";

Vue.use(Lazyload);

@Component({
  components: {
    "van-button": Button,
  }
})
export default class About extends Vue {
  private isNike: boolean = false;
  private inviterInfo: any = {};
  private show: boolean = false;
  private paramsObj: any = {};

  private async created() {
    this.paramsObj = getParams(location.href);
    // if (this.paramsObj.userId) {
    //     this.getinviterInfo(this.paramsObj.userId);
    //   }
    // 已经登录绑定过 在不在微信环境都是可以请求获取用户名称
    if (this.paramsObj.userId) {
      this.getinviterInfo(this.paramsObj.userId);
    }
    if (isWeiXin()) {
      this.show = false;
      // 没有登录绑定
      if (this.paramsObj.code) {
        alert("this.paramsObj.code：" + this.paramsObj.code)
        // 此时会存在一种特殊情况，url中的code是用户直接发送带有code的链接给对方的
        addCookie("code", this.paramsObj.code, 2);
        // 拿code去请求 用户绑定
        this.login(this.paramsObj.code, this.paramsObj.userId);
      } else if (getCookie("code")) {
        alert("getCookie：" + getCookie("code"))
        // console.log(getCookie("code"), "cookie");
        // 绑定;
        this.login(getCookie("code"), this.paramsObj.userId);
      } else {
        alert("code为空")
        // 获取code
        this.getCode();
      }
    } else {
      this.show = true;
    }
  }

  private getCode() {
    const _temp = location.href;
    alert("回调URL：" + _temp)
    location.href =
      "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx13534bb54b317489&redirect_uri=" +
      encodeURIComponent(_temp) +
      "&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect";
  }

  private async login(code, userId) {
    alert("请求登录：" + code)
    const wip = await Axios.get(
      "/teacher/login_by_wx?code=" + code
    );
    // const wip = await Axios.post(
    //   "/zhongkangdao/h5/teacher/fill_info?code=123"
    // );

    if (wip.succ) {
      // console.log("登录成功");
    } else {
      // this.getCode();

      alert("回调URL：" + wip.data.toString())
    }
  }

  private async getinviterInfo(userId) {
    this.isNike = true;
    // 此接口不需要登录，可以直接获取用户名
    const wipdata = await Axios.get(
      "/h5/users/invitation/inviter_info?inviterId=" + userId
    );
    if (wipdata.succ) {
      this.inviterInfo = wipdata.value;
    } else {
      this.isNike = false;
    }
  }

  private goDown() {
    // 下载是需要登录的
    if (isWeiXin()) {
      console.log("微信");
      if (this.paramsObj.code || getCookie("code")) {
        new Godownload();
      } else {
        this.getCode();
      }
    } else {
      // 可以直接下载
      console.log("qq");
      new Godownload();
    }

  }
}



