









import Vue from 'vue'
import Component from 'vue-class-component'
import axios from 'axios'

// 默认template中绑定的就是TSLearn VUE
@Component
export default class TSLearn extends Vue {
  // 初始化数据
  private msg: any = 123

  // 声明周期钩子
  mounted() {
    // this.greet()
    this.msg += 123;
  }

  // 计算属性
  get getMessage() {
    return 'computed 1' + this.msg
  }

  // 方法
  greet() {
    alert('greeting: ' + this.msg)
    // axios.defaults.baseURL = process.env.API_ROOT
    // axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest'
    axios
      .get('/teacher/getDetailBuyUserId?userId=108')
      .then(response => (this.msg += response.data.chartName))
  }
}



