/**
 * 返回值拦截
 */
class Check {
  public _checkStatus (response: any) {
    // console.log(response);
    // 如果http状态码正常，则直接返回数据
    if (response && (response.status === 200 || response.status === 304 || response.status === 400)) {
      if (response.data.succ) {
        return response.data
      } else {
        return response.data
      }
    }
    // 异常状态下，把错误信息返回去
    return {
      status: response.status,
      msg: '异常'
    }
  }

  public _checkCode (error: any) {
    // console.log(error.request.status);
    // 如果code异常(这里已经包括网络错误，服务器错误，后端抛出的错误)，可以弹出一个错误提示，告诉用户
    if (error.status === -404) {
      alert(error.msg)
    }
    if (error.data && (!error.data.success)) {
      alert(error.data.error_msg)
    }
    return error
  }

}

class Axios {
  private _Check: any

  constructor () {
    this._Check = new Check()
  }

  // 封装的GET请求
  public get (url: string, params: any = null) {
    return axios({
      url,
      params
    }).then(
      (response: any) => {
        return this._Check._checkStatus(response)
      }
    ).catch(
      (error: any) => {
        return this._Check._checkCode(error)
      }
    )
  }

  // 封装的POST请求
  public post (url: string, data: any = null) {
    return axios({
      method: 'post',
      url,
      data
      //   headers: {
      //     'X-Requested-With': 'XMLHttpRequest',
      //     'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      //   }
    }).then(
      (response) => {
        console.log('请求成功')
        return this._Check._checkStatus(response)
      }
    ).catch(
      (error) => {
        console.log('请求失败')
        return this._Check._checkCode(error)
      }
    )
  }
}

export default new Axios()



