export function getParams (url) {
  const arr = url.split('#')
  const paramsObj: any = {}
  // 确保有code参数
  if (arr[0].indexOf('?') !== -1) {
    arr[0] = (arr[0].split('?')[1]).split('&')
    // console.log(arr[0]);
    for (const item of arr[0]) {
      // console.log(item);
      const newarr = item.split('=')
      paramsObj[newarr[0]] = newarr[1]
    }
  }
  // 确保有userId参数
  if (arr[1].indexOf('?') !== -1) {
    arr[1] = arr[1].split('?')[1].split('=')
    paramsObj[arr[1][0]] = arr[1][1]
  }
  // console.log(paramsObj);
  alert(JSON.stringify(paramsObj))
  return paramsObj

}

function getParamsFromQueryOnly (url: String) {
  let index = url.indexOf('?')
  if (index > 0) {
    let query = url.substring(index + 1)
    let kvSrtrings = query.split('&')
    let params = {}
    for (let kvStr of kvSrtrings) {
      let kvarr = kvStr.split('=')
      params[kvarr[0]] = kvarr[1]
    }
    return params
  } else {
    return {}
  }
}

// 特殊case
// http://localhost:8080/#/red_packet?userId=210&id=504403158266811602
// http://localhost:8080?code=123&state=STATE#/red_packet?userId=210&id=504403158266811602
export function getParamsEx (url: String) {
  let fragmentIndex = url.indexOf('#')
  if (fragmentIndex > 0) {
    let firstUrl = url.substring(0, fragmentIndex)
    let fragmentStr = url.substring(fragmentIndex + 1)
    let params = getParamsFromQueryOnly(firstUrl)
    let paramsInFragment = getParamsFromQueryOnly(fragmentStr)
    for (let key in paramsInFragment) {
      params[key] = paramsInFragment[key]
    }
    return params
  } else {
    return getParamsFromQueryOnly(url)
  }
}

export function isWeiXin () {
  const ua: any = window.navigator.userAgent.toLowerCase()
  if (ua.match(/MicroMessenger/i) && ua.match(/MicroMessenger/i)[0].toLowerCase() === 'micromessenger') {
    return true
  } else {
    return false
  }
}

export function Source () {
  const ua: any = navigator.userAgent.toLowerCase()
  // 通过UA中的信息 判断是来自微信、APP还是浏览器
  if (ua.match(/MicroMessenger/i) && ua.match(/MicroMessenger/i)[0] === 'micromessenger') {
    return 'weixin'
    /* tslint:disable */
  } else if (ua.match(/PinKuios/i) && ua.match(/PinKuios/i)[0] === "pinkuios" || ua.match(/huazhu_android/i) && ua.match(/huazhu_android/i)[0] === "huazhu_android") {
    /* tslint:enable */
    return 'app'
  } else {
    return 'browser'
  }
}
export class Godownload {
  // public language: any = (navigator.browserLanguage || navigator.language).toLowerCase()
  constructor () {
    const navigators: any = this._versions()
    if (navigators.mobile) {// 判断是否是移动设备打开。browser代码在下面
      //   var ua = navigator.userAgent.toLowerCase(); //获取判断用的对象
      if (navigators.ios) {
        console.log('ios')
        location.href = 'https://itunes.apple.com/cn/app/id1448002442?mt=8'
      } else if (navigators.android) {
        console.log('android')
        // 是否在安卓浏览器打开
        location.href = 'https://a.app.qq.com/o/simple.jsp?pkgname=com.albatross'
        // el.setAttribute("href",
      } else {
        console.log('unknown mobile type')
      }
    } else {
      console.log('not mobile type')
    }
  }
  public _versions () {
    const u = navigator.userAgent
    const app = navigator.appVersion
    return { // 移动终端浏览器版本信息
      trident: u.indexOf('Trident') > -1, // IE内核
      presto: u.indexOf('Presto') > -1, // opera内核
      webKit: u.indexOf('AppleWebKit') > -1, // 苹果、谷歌内核
      gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') === -1, // 火狐内核
      mobile: !!u.match(/AppleWebKit.*Mobile.*/), // 是否为移动终端
      ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios终端
      android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, // android终端或uc浏览器
      iPhone: u.indexOf('iPhone') > -1, // 是否为iPhone或者QQHD浏览器
      iPad: u.indexOf('iPad') > -1, // 是否iPad
      webApp: u.indexOf('Safari') === -1 // 是否web应该程序，没有头部与底部
    }
  }

}



